"use strict";
Page({
    data: {
        nameValue: '',
    },
    onLoad: function (options) {
        var name = options.name;
        this.setData({
            nameValue: name,
        });
    },
    onSubmit: function () {
        wx.navigateBack({ backRefresh: true });
    },
    clearContent: function () {
        this.setData({
            nameValue: '',
        });
    },
});
